<template>
  <div class="classify">
    <van-nav-bar left-text="分类" @click-right="search">
      <template #right>
        <van-icon name="search" @click="goSearch" color="#000" size="24" />
      </template>
    </van-nav-bar>
    <div class="banner-box">
      <h2>猜你喜欢</h2>
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item, index) in bannerData" :key="index">
          <img class="auto-img" :src="item.img" @click="goClassifyList(item.gender,item.name)"/>
          <div class="banner-title">{{item.name}}</div>
        </van-swipe-item>
      </van-swipe>
    </div>

    <van-tabs
      class="tabs-box"
      sticky
      offset-top="81px"
      line-width="15px"
      color="#f99222"
      animated
      swipeable
      v-model="active"
    >
      <van-tab :title="item.tab" v-for="(item,index) in classifyData" :key="index">
        <div class="content">
          <div
            class="type-box"
            v-for="(v,i) in item.data"
            :key="i"
            @click="goClassifyList(index,v.name)"
          >
            <div class="img-box">
              <img class="auto-img" :src="v.bookCover" alt />
            </div>
            <div class="type-name">{{v.name}}</div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import "../../assets/less/classify.less";
export default {
  name: "Classify",
  data() {
    return {
      active: 0,
      // 分类数据
      classifyData: {
        male: { tab: "男生", data: [] },
        female: { tab: "女生", data: [] },
        picture: { tab: "漫画", data: [] },
        press: { tab: "出版", data: [] },
      },
      bannerData: [
        {
          gender: "female",
          name: "古代言情",
          img: require("../../assets/img/yanqing.jpg"),
        },
        {
          gender: "male",
          name: "玄幻",
          img: require("../../assets/img/xuanhuan.jpg"),
        },
        {
          gender: "male",
          name: "仙侠",
          img: require("../../assets/img/xianxia.jpg"),
        },
        {
          gender: "female",
          name: "科幻",
          img: require("../../assets/img/kehuan.jpg"),
        },
      ],
    };
  },
  created() {
    this.getClassifyData();
  },
  methods: {
    search() {},
    getClassifyData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/categories",
      })
        .then((result) => {
          // console.log(result);
          this.$toast.clear();
          for (let key in this.classifyData) {
            this.classifyData[key].data = result.data[key];
            this.classifyData[key].data.map((v) => {
              v.bookCover = unescape(v.bookCover[0].slice(7));
            });
          }
          // console.log("this.classifyData==>", this.classifyData);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    goClassifyList(gender, major) {
      this.$router.push({ name: "ClassifyList" ,query:{gender,major}});
    },

    // 前往搜索页面
    goSearch(){
      this.$router.push({name:"Search"})
    }
  },
};
</script>
